import React, { useState } from "react";
import "./Navbar.css";
import { FcMinus, FcMenu } from "react-icons/fc";
import { Link } from "react-router-dom";

export default function Navbar() {
  const [click, setClick] = useState(false);
  // const [toggle, setToggle] = useState(true);

  const handleClick = () => {
    //   var x = document.querySelector("ul");
    //   if (x.style.display === "none") {
    //     x.style.display = "flex";
    //   } else {
    //     x.style.display = "none";
    //   }
    setClick(!click);
  };

  // const handleToggle = () => {
  //   setToggle(!toggle);
  // };
  // console.log(toggle);

  return (
    <div className="header">
      <div className="header-component">
        <Link to="/">
          <h2 className="logo">THAPA</h2>
        </Link>
        <div className="mobileIcon" onClick={handleClick}>
          {click ? <FcMinus /> : <FcMenu />}
        </div>

        <ul className={click ? "nav-link-mobile" : "nav-link"}>
          <Link to="/">
            <li className="links">Home</li>
          </Link>
          <Link to="/service">
            <li className="links">Services</li>
          </Link>
          <li className="links">About</li>
        </ul>
        <Link to="signUp">
          <button className="singup">Sign Up</button>
        </Link>
      </div>
    </div>
  );
}
